export default {
  // 编写的就是Vue组件实例 配置项，通过一定语法，组件混入到 组件内部
  // data. method computer 生命周期函数
  // 如果提供给了同名的data method 组件内优先级更高
//   如果编写了生命周期函数 和 页面的生命周期函数 会整理到数组中，同一管理，统一执行
  // 先执行混入里的生命周期
  methods: {
    loginConfirm () {
      // 判断token是否存在
      // 1.不在-未登录 弹框提示
      // 2.在 -已登录
      if (!this.$store.getters.token) {
        // 弹框提示
        this.$dialog.confirm({
          title: '温馨提示',
          message: '此时需要登录才能继续操作',
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // 如果希望跳转到登录页
            // 并且登录后回跳加入购物车页面
            // 则需要携带当前的路径i地址
            // this.$router.fullPath(带参数)
            this.$router.replace({
              path: '/login',
              query: {
                backUrl: this.$router.fullPath
              }
            })
          })
          .catch(() => {})
        return true
      }
      return false
    }
  }
}
